<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a @click.prevent="prevButton(page)"
         class="pagination__link pagination__link--arrow"
         :class="checkPrevDisable === true? 'pagination__link--disabled' : ''"
         aria-label="Предыдущая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a href="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}"
         @click.prevent="paginate(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>
    <li class="pagination__item">
      <a @click.prevent="lastButton(page)"
         class="pagination__link pagination__link--arrow"
         :class="checkLastDisable === true? 'pagination__link--disabled' : ''"
         aria-label="Следующая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>

export default {
  model: {
    prop: 'page',
    event: 'paginate'
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
    checkPrevDisable () {
      return this.page - 1 < 1;
    },
    checkLastDisable () {
      return this.page + 1 > this.pages;
    }
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page)
    },
    prevButton(page) {
      page = page - 1;
      if (page < 1) {
        page = 1;
      }
      this.paginate(page)
    },
    lastButton(page) {
      page = page + 1;
      if (page > this.pages) {
        page = this.pages;
      }
      this.paginate(page)
    }
  }
}

</script>