<template>
  <div class="cart__block">
    <div v-if="cartLoaded && !cartError">
      <ul class="cart__orders">
        <li class="cart__order" v-for="item in products" :key="item.productId">
          <h3>{{ item.product.title }}</h3>
          <b>{{ (getProductQuantity(item) * item.product.price) | numberFormat }} ₽</b>
          <span>Артикул: {{ item.product.id }}</span>
        </li>
      </ul>

      <div class="cart__total">
        <p>Доставка: <b>{{ deliveryPrice | numberFormat }} ₽</b></p>
        <p>Итого: <b>{{ products.length }}</b> товар{{ theAfunction }} на сумму <b>{{ priceWithDelivery | numberFormat }}
          ₽</b></p>
      </div>

      <button class="cart__button button button--primery" type="submit" v-show="products.length && exists !== false">
        Оформить заказ
      </button>
    </div>
    <div class="cart__block" v-else>
      <div class="cart__total">
        <p><img src="img/svg/spinner.svg"> Товары загружаются... </p>
      </div>
    </div>
  </div>

</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import lastLetterChecker from "@/helpers/lastLetterChecker";

export default {
  name: "SidebarRight.vue",
  props: ['deliveryPrice', 'products', 'totalPrice', 'exists'],
  computed: {
    cartLoaded() {
      return this.$store.state.cartLoaded
    },
    cartError() {
      return this.$store.state.cartError
    },
    theAfunction() {
      return this.lastLetterChecker();
    },
    priceWithDelivery() {
      return this.deliveryPrice + this.totalPrice;
    }
  },
  methods: {
    lastLetterChecker,
    getProductQuantity(item) {
      if (item.amount) {
        return item.amount;
      } else if (item.quantity) {
        return item.quantity;
      } else {
        return 1;
      }
    }
  },
  filters: {numberFormat},
}
</script>

