<template>
  <div>
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
      <img :src="product.image" :alt="product.title">
    </router-link>
    <h3 class="catalog__title">
      <a href="#" @click.prevent="gotoPage('product', {id: product.id})">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.price | numberFormat }} ₽
    </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="color in productColor(product.colors)" :key="color.id">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="color.id" v-model="checkedColor">
          <span class="colors__value" :style="{backgroundColor: color.code}">
          </span>
        </label>
      </li>
    </ul>
  </div>
</template>

<script>
import colors from "@/data/colors";
import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";
import productColor from "@/helpers/productColor";

export default {
  computed: {
    colors() {
      return colors;
    },
  },
  data() {
    return {
      checkedColor: this.product.currentColor
    }
  },
  props: ['product'],
  filters: {
    numberFormat
  },
  methods: {
    gotoPage,
    productColor,
  }
}

</script>