<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <SidebarLeft :price-to.sync="filterPriceTo" :price-from.sync="filterPriceFrom"
                   :category-id.sync="filterCategoryId" :color-id.sync="filterColorId"/>
      <section class="catalog">
        <div v-show="productsLoading === true">
          Загрузка товаров... Подождите пожалуйста
        </div>
        <div v-if="productsLoadingFailed">
          Произошла ошибка. Перезагрузите страницу или обратитесь к администратору
          <button @click.prevent="loadProducts">Перезагрузить страницу</button>
        </div>

        <ProductList :products="products"/>

        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage"/>
      </section>
    </div>
  </main>
</template>

<script>
import axios from 'axios';
import {API_BASE_URL} from "@/config"
import SidebarLeft from "@/components/SidebarLeft";
import ProductList from "@/components/ProductList";
import BasePagination from "@/components/BasePagination";

export default {
  name: "MainPage.vue",
  components: {SidebarLeft, ProductList, BasePagination},
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,
      page: 1,
      productsPerPage: 6,
      productData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    }
  },
  computed: {

    products() {
      return this.productData ?
          this.productData.items.map(product => {
            return {
              ...product,
              image: product.image.file.url
            }
          })
          : [];
    },
    countProducts() {
      return this.productData ? this.productData.pagination.total : 0;
    }
  },
  created() {
    this.loadProducts();
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(API_BASE_URL + '/api/products/', {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            colorId: this.filterColorId
          }
        })
            .then(response => this.productData = response.data)
            .catch(() => this.productsLoadingFailed = true)
            .then(() => this.productsLoading = false)
      }, 0)
    }
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    }
  }
}
</script>

