<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'main'}" class="breadcrumbs__link">Каталог</router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ $store.state.cartProducts.length }}
        товар{{ theAfunction }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list" v-show="cartLoaded && !cartError">
            <CartItem v-for="item in products" :key="item.productId" :item="item"/>
          </ul>
          <div v-if="!cartLoaded"> Загрузка списка товаров. Подождите пожалуйста </div>
          <div v-if="cartError"> Загрузка списка товаров не удалась. Перезагрузите страницу пожалуйста </div>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link
              tag="button"
              :to="{name: 'order'}"
              class="cart__button button button--primery"
              type="submit"
              v-show="$store.state.cartProducts.length">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import lastLetterChecker from "@/helpers/lastLetterChecker";
import CartItem from "@/components/CartItem";
import {mapGetters} from 'vuex';

export default {
  name: "CartPage.vue",
  components: {CartItem},
  computed: {
    ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice'}),
    theAfunction() {
      return this.lastLetterChecker();
    },
    cartLoaded() { return this.$store.state.cartLoaded },
    cartError() { return this.$store.state.cartError }
  },
  methods: {
    lastLetterChecker
  },
  filters: {numberFormat},
}
</script>

