<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'main'}" class="breadcrumbs__link">Каталог</router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'cart'}" class="breadcrumbs__link">Корзина</router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ $store.state.cartProducts.length }}
        товар{{ theAfunction }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order" v-show="formSending === false">
        <div class="cart__field">
          <div class="cart__data">
            <base-form-text
                title="ФИО"
                :error="formError.name"
                placeholder="Введите ваше полное имя"
                v-model="formData.name"
            ></base-form-text>

            <base-form-text
                title="Адрес доставки"
                :error="formError.address"
                placeholder="Введите ваш адрес"
                v-model="formData.address"
            ></base-form-text>

            <base-form-text
                title="Телефон"
                :error="formError.phone"
                type="tel"
                placeholder="Введите ваш телефон"
                v-model="formData.phone"
            ></base-form-text>

            <base-form-text
                title="Email"
                :error="formError.email"
                type="email"
                placeholder="Введи ваш Email"
                v-model="formData.email"
            ></base-form-text>

            <base-form-textarea
                title="Ваши пожелания"
                :error="formError.comment"
                placeholder="Ваши пожелания"
                v-model="formData.comment"
            ></base-form-textarea>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <sidebar-right :delivery-price="500" :products="products" :total-price="totalPrice" :exists="true">

        </sidebar-right>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
      <div v-if="formSending === true">
        Корзина отправляется...
      </div>
    </section>
  </main>
</template>

<script>
import lastLetterChecker from "@/helpers/lastLetterChecker";
import BaseFormText from "@/components/BaseFormText";
import SidebarRight from "@/components/SidebarRight";
import BaseFormTextarea from "@/components/BaseFormTextarea";
import axios from "axios";
import {API_BASE_URL} from "@/config"
import {mapGetters} from "vuex";

export default {
  name: "OrderPage.vue",
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      formSending: false
    }
  },
  components: {BaseFormText, BaseFormTextarea, SidebarRight},
  computed: {
    ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice'}),
    theAfunction() {
      return this.lastLetterChecker();
    },
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.formSending = true;

      axios
          .post(API_BASE_URL + '/api/orders', {
                ...this.formData
              },
              {
                params: {
                  userAccessKey: this.$store.state.userAccessKey
                }
              })
          .then(response => {
            this.formSending = false;
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.$router.push({
              name: 'orderInfo', params: {
                id: response.data.id
              }
            })
          })
          .catch(error => {
            this.formSending = false;
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
    },
    lastLetterChecker
  }
}
</script>

